'use client';

import { useState, useEffect, useRef } from 'react';
import QRCode from 'qrcode';

interface QRGeneratorProps {
    url?: string;
    hubSlug?: string;
}

export default function QRGenerator({ url, hubSlug = 'demo' }: QRGeneratorProps) {
    const canvasRef = useRef<HTMLCanvasElement>(null);
    const [qrUrl, setQrUrl] = useState('');
    const [customUrl, setCustomUrl] = useState('');
    const [size, setSize] = useState(200);
    const [fgColor, setFgColor] = useState('#00C853');
    const [bgColor, setBgColor] = useState('#000000');
    const [isDownloading, setIsDownloading] = useState(false);

    const baseUrl = typeof window !== 'undefined' ? window.location.origin : 'http://localhost:3000';
    const defaultUrl = url || `${baseUrl}/${hubSlug}`;
    const activeUrl = customUrl || defaultUrl;

    useEffect(() => {
        if (!canvasRef.current) return;

        QRCode.toCanvas(canvasRef.current, activeUrl, {
            width: size,
            margin: 2,
            color: {
                dark: fgColor,
                light: bgColor,
            },
            errorCorrectionLevel: 'M',
        }).catch(console.error);

        // Also generate data URL for download
        QRCode.toDataURL(activeUrl, {
            width: size * 2,
            margin: 2,
            color: {
                dark: fgColor,
                light: bgColor,
            },
            errorCorrectionLevel: 'H',
        }).then(setQrUrl).catch(console.error);
    }, [activeUrl, size, fgColor, bgColor]);

    const handleDownload = () => {
        if (!qrUrl) return;
        setIsDownloading(true);

        const link = document.createElement('a');
        link.download = `qr-${hubSlug}-${Date.now()}.png`;
        link.href = qrUrl;
        link.click();

        setTimeout(() => setIsDownloading(false), 500);
    };

    const handleCopyUrl = async () => {
        try {
            await navigator.clipboard.writeText(activeUrl);
            alert('URL copied to clipboard!');
        } catch (err) {
            console.error('Failed to copy:', err);
        }
    };

    const presets = [
        { fg: '#00C853', bg: '#000000', label: 'Green/Black' },
        { fg: '#FFFFFF', bg: '#000000', label: 'White/Black' },
        { fg: '#000000', bg: '#FFFFFF', label: 'Black/White' },
        { fg: '#00C853', bg: '#FFFFFF', label: 'Green/White' },
        { fg: '#3B82F6', bg: '#000000', label: 'Blue/Black' },
    ];

    return (
        <div className="space-y-6">
            {/* QR Code Display */}
            <div className="flex flex-col items-center gap-4 p-6 bg-[var(--bg-secondary)] border border-[var(--border)] rounded-xl">
                <canvas
                    ref={canvasRef}
                    className="rounded-lg"
                    style={{ background: bgColor }}
                />
                <p className="text-sm text-[var(--text-muted)] text-center break-all max-w-[250px]">
                    {activeUrl}
                </p>
            </div>

            {/* Customization */}
            <div className="space-y-4">
                {/* Custom URL */}
                <div>
                    <label className="block text-sm text-[var(--text-secondary)] mb-2">
                        Custom URL <span className="text-[var(--text-muted)]">(optional)</span>
                    </label>
                    <input
                        type="url"
                        value={customUrl}
                        onChange={(e) => setCustomUrl(e.target.value)}
                        placeholder={defaultUrl}
                        className="w-full px-3 py-2 bg-[var(--bg)] border border-[var(--border)] rounded-lg text-[var(--text)] text-sm focus:border-[var(--accent)] outline-none"
                    />
                </div>

                {/* Size */}
                <div>
                    <label className="block text-sm text-[var(--text-secondary)] mb-2">
                        Size: {size}px
                    </label>
                    <input
                        type="range"
                        min="100"
                        max="400"
                        value={size}
                        onChange={(e) => setSize(parseInt(e.target.value))}
                        className="w-full accent-[var(--accent)]"
                    />
                </div>

                {/* Colors */}
                <div className="grid grid-cols-2 gap-4">
                    <div>
                        <label className="block text-sm text-[var(--text-secondary)] mb-2">QR Color</label>
                        <div className="flex items-center gap-2">
                            <input
                                type="color"
                                value={fgColor}
                                onChange={(e) => setFgColor(e.target.value)}
                                className="w-10 h-10 rounded border-0 cursor-pointer"
                            />
                            <input
                                type="text"
                                value={fgColor}
                                onChange={(e) => setFgColor(e.target.value)}
                                className="flex-1 px-2 py-1 bg-[var(--bg)] border border-[var(--border)] rounded text-sm text-[var(--text)]"
                            />
                        </div>
                    </div>
                    <div>
                        <label className="block text-sm text-[var(--text-secondary)] mb-2">Background</label>
                        <div className="flex items-center gap-2">
                            <input
                                type="color"
                                value={bgColor}
                                onChange={(e) => setBgColor(e.target.value)}
                                className="w-10 h-10 rounded border-0 cursor-pointer"
                            />
                            <input
                                type="text"
                                value={bgColor}
                                onChange={(e) => setBgColor(e.target.value)}
                                className="flex-1 px-2 py-1 bg-[var(--bg)] border border-[var(--border)] rounded text-sm text-[var(--text)]"
                            />
                        </div>
                    </div>
                </div>
            </div>

            {/* Actions */}
            <div className="flex gap-3">
                <button
                    onClick={handleDownload}
                    disabled={isDownloading}
                    className="flex-1 px-4 py-2.5 bg-[var(--accent)] hover:opacity-90 text-black font-bold rounded-lg transition-opacity flex items-center justify-center gap-2"
                >
                    {isDownloading ? (
                        <span className="animate-spin">‚è≥</span>
                    ) : (
                        <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-4l-4 4m0 0l-4-4m4 4V4" />
                        </svg>
                    )}
                    Download PNG
                </button>
                <button
                    onClick={handleCopyUrl}
                    className="px-4 py-2.5 bg-[var(--bg-secondary)] border border-[var(--border)] hover:border-[var(--accent)] text-[var(--text)] rounded-lg transition-colors flex items-center gap-2"
                >
                    <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
                    </svg>
                    Copy URL
                </button>
            </div>

            {/* Preset Colors */}
            <div>
                <label className="block text-sm text-[var(--text-secondary)] mb-2">Presets</label>
                <div className="flex gap-2 flex-wrap">
                    {presets.map((preset, i) => (
                        <button
                            key={i}
                            onClick={() => {
                                setFgColor(preset.fg);
                                setBgColor(preset.bg);
                            }}
                            className="px-3 py-1.5 text-xs bg-[var(--bg-secondary)] border border-[var(--border)] rounded-lg hover:border-[var(--accent)] transition-colors flex items-center gap-2"
                        >
                            {/* Color preview squares */}
                            <span
                                className="w-3 h-3 rounded border border-[var(--border)]"
                                style={{ backgroundColor: preset.fg }}
                            />
                            <span
                                className="w-3 h-3 rounded border border-[var(--border)]"
                                style={{ backgroundColor: preset.bg }}
                            />
                            <span className="text-[var(--text)]">{preset.label}</span>
                        </button>
                    ))}
                </div>
            </div>
        </div>
    );
}
